import React, { Component } from 'react';
import { TableContainer, Table, TableHead, TableRow, TableCell, TableBody, CircularProgress } from '@material-ui/core';
import axios from 'axios';


class CAList extends Component{

  constructor(props){
    super(props);
    this.state={
      rows:[],
      viewDefaulter: false,
      loadingRows: true,
      pagination:{
        rowsPerPage: 5,
        page: 1
      }
    };
  }

  componentDidMount(){
    axios.get('/ca/fetchAllCAs').then((response)=>{
      this.setState({
        rows: [...response.data],
        loadingRows: false
      })
    }).catch((err)=>{
      console.log(err.response);
      console.log('ERROR', err);      
      this.setState({
        loadingRows: false
      })
    });
  }

  render(){
    const { loadingRows, rows, pagination } = this.state || {};
    const columns=['Name','Email', 'Mobile', 'Whatsapp', 'Institute', 'Address'];
    if(loadingRows){
      return (<CircularProgress style={{ marginTop: '15vh' }} />);
    }
    return(
      <React.Fragment>
      <TableContainer style={{ marginTop: '2vh' }}>
        
        <Table stickyHeader aria-label="sticky table">
          <TableHead>
            <TableRow>
              {columns.map(column => (
                <TableCell
                  key={column}
                  align="left"
                >
                  {column}
                </TableCell>
              ))}
            </TableRow>
          </TableHead>
          <TableBody>
            {rows.map(row => {
              return (
                <TableRow hover role="checkbox" key={row._id}>
                  {columns.map(column => (
                    <TableCell key={column} align="left">
                      {column==='Address' && `${row.city}, ${row.state} - ${row.zip}`}
                      {column!='Address' && row[column.toLowerCase()]}
                    </TableCell>
                    
                  ))}
                </TableRow>
              );
            })}
          </TableBody>
        </Table>
      </TableContainer>
      {/* <TablePagination
        rowsPerPageOptions={[5, 10]}
      component="div"
      count={rows.length}
      rowsPerPage={pagination.rowsPerPage}
      page={pagination.page}
      onChangePage={(event, page)=>{
        this.setState({
          pagination:{
            page
          }
        })
      }}
      onChangeRowsPerPage={(event, rowsPerPage)=>{
        this.setState({
          pagination:{
            rowsPerPage
          }
        })
        }}
      /> */}
      </React.Fragment>
    );
  }
};

export default CAList;