import React, { Component } from 'react';
import { AppBar, Typography, Toolbar, Button, Tabs, Tab, Paper, Grid, Divider, TextField, Snackbar } from '@material-ui/core';
import WorkshopList from './Workshop';
import CAList from './CA';
import Event from './Event';
import styled from 'styled-components';


class Super extends Component{

  constructor(props){
    super(props);
    this.state={
      seletedTab: 0,
      errorMessage:'',
      login: false,
      loginVal:{
        username: '',
        password: ''
      }
    };
  }

  handleLogin(){
    const {loginVal} = this.state;
    if((loginVal.password==='hailgst2020' && (loginVal.username==='siddhartha' || loginVal.username==='shivam')) || 
      (loginVal.password==='itsme' && (loginVal.username==='major'))
    ){
      this.setState({
        login: true
      })
    }
    else{
      this.setState({
        errorMessage: 'Invalid Details'
      })
    }
  }

  handleFormChange(field, val){
    this.setState((prevState)=>({
      ...prevState,
      loginVal:{
        ...prevState.loginVal,
        [field]: val
      }
    }));
  }

  render(){
    const {login, loginVal} = this.state;

    return(
      <React.Fragment>

        <AppBar position="static">
        <Tabs
          variant="fullWidth"
          aria-label="nav tabs example"
          onChange={(event, val)=>{
            this.setState({
              seletedTab: val
            });
          }}
          style={{ backgroundColor: '#0285a1' }}
        >
          <Tab
            component="a"
            onClick={event => {
              event.preventDefault();
            }}
            label="Campus Ambassdor"
            href="/lo"
            id="ca"
          />
          <Tab
            component="a"
            onClick={event => {
              event.preventDefault();
            }}
            label="Workshop"
            href=""
            id="workshop"
          />
          <Tab
            component="a"
            onClick={event => {
              event.preventDefault();
            }}
            label="Event"
            href=""
            id="event"
          />
        </Tabs>
      </AppBar>
      {this.state.seletedTab===0 && (<CAList />)}
      {this.state.seletedTab===1 && (<WorkshopList />)}
      {this.state.seletedTab===2 && (<Event />)}

    </React.Fragment>
    );
  }
};

export default Super;