import React, { Component } from 'react';
import { AppBar, Typography, Toolbar, Button, Tabs, Tab, Paper, Grid, Divider, TextField, Snackbar } from '@material-ui/core';
import Super from './Super';
import styled from 'styled-components';

const FormPaper = styled(Paper)`
  width: 40vw;
  min-height: 60vh;
  padding: 2vh 2vw;
  margin: 5vw 28vw 2vw;

  @media(orientation:portrait) {
    width: 100vw;
    margin: 0;
  }
`;

const LoginContainer = styled.div`
  height: 100vh;
  width: 100vw;
  margin-top: -10vh;
  background-color: #0285a1;
`;


class Admin extends Component{

  constructor(props){
    super(props);
    this.state={
      seletedTab: 0,
      errorMessage:'',
      login: false,
      loginVal:{
        username: '',
        password: ''
      }
    };
  }

  handleLogin(){
    const {loginVal} = this.state;
    if((loginVal.password==='hailgst2020' && (loginVal.username==='siddhartha' || loginVal.username==='shivam')) || 
      (loginVal.password==='itsme' && (loginVal.username==='major'))
    ){
      this.setState({
        login: true
      })
    }
    else{
      this.setState({
        errorMessage: 'Invalid Details'
      })
    }
  }

  handleFormChange(field, val){
    this.setState((prevState)=>({
      ...prevState,
      loginVal:{
        ...prevState.loginVal,
        [field]: val
      }
    }));
  }

  render(){
    const {login, loginVal} = this.state;
    if(!login){
      return(
        <LoginContainer>
          <FormPaper>
            <Grid item xs={12} sm={12} style={{margin: '5% 0%', textAlign: 'center', fontSize: '3.5vh'}}>
              Login To Tecnoesis 2020
            </Grid>
            <Divider />
            <Grid item xs={8} sm={8} style={{margin: '2vh 0' }}>
              <TextField 
                label="Username"
                required
                name="username"
                value={loginVal.username}
                onChange={(event)=>{
                  this.handleFormChange('username', event.target.value)
                }}
                fullWidth
              />
            </Grid>
            <Grid  item xs={8} sm={8} style={{margin: '2vh 0' }}>
              <TextField 
                label="Password"
                required
                name="password"
                type="password"
                onChange={(event)=>{
                  this.handleFormChange('password', event.target.value)
                }}
                value={loginVal.password}
                fullWidth
              />
            </Grid>
            <Button variant="contained" color="primary" style={{margin: '3vh 0' }}
              onClick={()=>{
                this.handleLogin();
              }}
              disabled={loginVal.username==='' || loginVal.password===''}
            >
              Login
            </Button>
          </FormPaper>
          <Snackbar
            open={this.state.errorMessage!==''}
            anchorOrigin={{ horizontal: 'right', vertical: 'top' }}
            message={this.state.errorMessage}
            variant="error"
            onClose={()=> {
              this.setState({
                error: false
              })
            }}
            autoHideDuration={5000}
          />
        </LoginContainer>
      );
    }
    return(
      <React.Fragment>
        <AppBar position="static" style={{ backgroundColor: '#0285a1' }}>
        <Toolbar>
            <Typography variant="h5">
            {`Hi ${loginVal.username.charAt(0).toUpperCase()}${loginVal.username.slice(1)},`}
            </Typography>
            <span style={{ marginLeft: 'auto' }}>
             
                <Button color="inherit" onClick={()=>{
                  this.setState({
                    login: false
                  })
                }}>
                  Logout
                </Button>
            </span>
          </Toolbar>
        </AppBar>
        <Super />
    </React.Fragment>
    );
  }
};

export default Admin;