import React, { Component } from 'react';
import { TableContainer, Table, TableHead, TableRow, TableCell, TableBody, CircularProgress, FormControlLabel, Switch, Typography, Grid, InputLabel, Select, MenuItem, Button } from '@material-ui/core';
import axios from 'axios';
import moduleInfo from './eventConfig.json';


class Event extends Component{

  constructor(props){
    super(props);
    this.state={
      rows:[],
      module:'',
      moduleIndex: -1,
      event:'',
      eventIndex: -1,
      viewDefaulter: false,
      loadingRows: false,
      pagination:{
        rowsPerPage: 5,
        page: 1
      }
    };
  }
  
  handleModuleSelection(field, value){
    this.setState({
      [field]: value
    })
  }

  fetchModule(){
    this.setState({
      loadingRows: true
    });
    const { moduleIndex, eventIndex } = this.state || {};
    const data={
      module: moduleInfo[moduleIndex].name
    };
    if(eventIndex>-1){
      data.event=moduleInfo[moduleIndex].events[eventIndex];
    }
    axios({
      method: 'POST',
      url: '/workshop/fetchEvent',
      data
    }).then((response)=>{
      this.setState({
        rows: [...response.data],
        loadingRows: false
      })
    }).catch((err)=>{
      console.log(err.response);    
      this.setState({
        loadingRows: false
      })
    });
  }

  render(){
    const { loadingRows, rows, viewDefaulter, moduleIndex, eventIndex } = this.state || {};
    const columns=['Team Name','Email', 'Mobile', 'Event', 'Institute', 'Kit'];
    const viewRecord=rows.filter((row)=>{
      return (row.payment_id!==null && !viewDefaulter) || viewDefaulter;
    });
    if(loadingRows){
      return (<CircularProgress style={{ marginTop: '15vh' }} />);
    }
    return(
      <React.Fragment>
        <Grid container >
          <Grid item xs={4}>
            <InputLabel id="demo-simple-select-label">Module</InputLabel>
            <Select
              labelId="demo-simple-select-label"
              id="demo-simple-select"
              value={moduleIndex}
              onChange={(event)=>{
                this.handleModuleSelection('moduleIndex', event.target.value)
              }}
            >
              <MenuItem value="" disabled>
                Select Module
              </MenuItem>
              {moduleInfo.map((event, index)=>{
                return(
                  <MenuItem key={event.name} value={index}>{event.name.toUpperCase()}</MenuItem>
                );
              })}
              
            </Select>  
          </Grid>
          <Grid item xs={4}>
              <InputLabel id="demo-simple-select-label">Events</InputLabel>
              <Select
                labelId="demo-simple-select-label"
                id="demo-simple-select"
                value={eventIndex}
                onChange={(event)=>{
                  this.handleModuleSelection('eventIndex', event.target.value)
                }}
              >
                <MenuItem value={-1} >
                  All
                </MenuItem>
                {moduleIndex>-1 && moduleInfo[moduleIndex].events.map((event, index)=>{
                  return(
                    <MenuItem key={event.name} value={index}>{event.toUpperCase()}</MenuItem>
                  );
                })}                
              </Select>
             </Grid>
              <Grid item xs={4}>
                <Button
                  disabled={moduleIndex<0 || eventIndex<-1}
                 variant="contained" onClick={()=>{
                  this.fetchModule()
                }}>
                  Find
                </Button>
              </Grid>
        </Grid>
        <div style={{ display: 'flex', justifyContent: 'space-between', padding: '2% 2% 0' }}>
        <Typography component="h3">
          {viewRecord.length} records found.
        </Typography>
        <FormControlLabel
          control={
            <Switch checked={viewDefaulter} onChange={()=>{
              this.setState((prevState)=>({
                ...prevState,
                viewDefaulter: !prevState.viewDefaulter
              }))
            }} value="checkedA" />
          }
          label="View Defaulters"
        />
      </div>
        <TableContainer style={{ marginTop: '2vh' }}>
          
          <Table stickyHeader aria-label="sticky table">
            <TableHead>
              <TableRow>
                <TableCell align="left" style={{ maxWidth: '0px' }}>
                  S.No.  
                </TableCell>
                {columns.map(column => (
                  <TableCell
                    key={column}
                    align="left"
                  >
                    {column}
                  </TableCell>
                ))}
              </TableRow>
            </TableHead>
            <TableBody>
              {viewRecord.map((row,index) => {
                if(viewDefaulter && !row.payment_id){
                  return (
                    <TableRow hover role="checkbox" key={row._id}>
                      <TableCell align="left" style={{ maxWidth: '0px' }}>
                        {index+1}    
                      </TableCell>
                      {columns.map((column) => (
                        <TableCell key={column} align="left">
                          {column==='Kit' && row.fee}
                          {column==='Team Name' && (row.teamName || row.member[0])}
                          {column!='Kit' && column!='Team Name' && row[column.toLowerCase()]}
                        </TableCell>
                        
                      ))}
                    </TableRow>
                  );
                }
                else if(!viewDefaulter && row.payment_id){
                  return (
                    <TableRow hover role="checkbox" key={row._id}>
                      <TableCell align="left" style={{ maxWidth: '0px' }}>
                        {index+1}    
                      </TableCell>
                      {columns.map(column => (
                        <TableCell key={column} align="left">
                          {column==='Kit' && row.fee}
                          {column==='Team Name' && (row.teamName || row.member[0])}
                          {column!=='Kit' && column!=='Team Name' && row[column.toLowerCase()]}
                        </TableCell>
                        
                      ))}
                    </TableRow>
                  );
                }
              })}
            </TableBody>
          </Table>
        </TableContainer>
        {/* <TablePagination
          rowsPerPageOptions={[5, 10]}
          component="div"
          count={rows.length}
          rowsPerPage={pagination.rowsPerPage}
          page={pagination.page}
          onChangePage={(event, page)=>{
            this.setState({
              pagination:{
                page
              }
            })
          }}
          onChangeRowsPerPage={(event, rowsPerPage)=>{
            this.setState({
              pagination:{
                rowsPerPage
              }
            })
          }}
        /> */}
      </React.Fragment>
    );
  }
};

export default Event;