import React, { Component } from 'react';
import { TableContainer, Table, TableHead, TableRow, TableCell, TableBody, CircularProgress, FormControlLabel, Switch, Typography } from '@material-ui/core';
import axios from 'axios';


class WorkshopList extends Component{

  constructor(props){
    super(props);
    this.state={
      rows:[],
      viewDefaulter: false,
      loadingRows: true,
      pagination:{
        rowsPerPage: 5,
        page: 1
      }
    };
  }

  componentDidMount(){
    axios.get('/workshop/fetchAllWorkshops').then((response)=>{
      this.setState({
        rows: [...response.data],
        loadingRows: false
      })
    }).catch((err)=>{
      console.log(err.response);
      console.log('ERROR', err);      
      this.setState({
        loadingRows: false
      })
    });
  }

  render(){
    const { loadingRows, rows, viewDefaulter } = this.state || {};
    const columns=['Name','Email', 'Mobile', 'Workshop', 'Institute', 'Address'];
    const viewRecord=rows.filter((row)=>{
      return (row.payment_id!==null && !viewDefaulter) || viewDefaulter;
    });
    if(loadingRows){
      return (<CircularProgress style={{ marginTop: '15vh' }} />);
    }
    return(
      <React.Fragment>
        <div style={{ display: 'flex', justifyContent: 'space-between', padding: '2% 2% 0' }}>
        <Typography component="h3">
          {viewRecord.length} records found.
        </Typography>
        <FormControlLabel
          control={
            <Switch checked={viewDefaulter} onChange={()=>{
              this.setState((prevState)=>({
                ...prevState,
                viewDefaulter: !prevState.viewDefaulter
              }))
            }} value="checkedA" />
          }
          label="View Defaulters"
        />
      </div>
        <TableContainer style={{ marginTop: '2vh' }}>
          
          <Table stickyHeader aria-label="sticky table">
            <TableHead>
              <TableRow>
                <TableCell align="left" style={{ maxWidth: '0px' }}>
                  S.No.  
                </TableCell>
                {columns.map(column => (
                  <TableCell
                    key={column}
                    align="left"
                  >
                    {column}
                  </TableCell>
                ))}
              </TableRow>
            </TableHead>
            <TableBody>
              {viewRecord.map((row,index) => {
                return (
                  <TableRow hover role="checkbox" key={row._id}>
                    <TableCell align="left" style={{ maxWidth: '0px' }}>
                      {index+1}    
                    </TableCell>
                    {columns.map(column => (
                      <TableCell key={column} align="left">
                        {column==='Address' && `${row.city}, ${row.state} - ${row.zip}`}
                        {column!='Address' && row[column.toLowerCase()]}
                      </TableCell>
                      
                    ))}
                  </TableRow>
                );
              })}
            </TableBody>
          </Table>
        </TableContainer>
        {/* <TablePagination
          rowsPerPageOptions={[5, 10]}
          component="div"
          count={rows.length}
          rowsPerPage={pagination.rowsPerPage}
          page={pagination.page}
          onChangePage={(event, page)=>{
            this.setState({
              pagination:{
                page
              }
            })
          }}
          onChangeRowsPerPage={(event, rowsPerPage)=>{
            this.setState({
              pagination:{
                rowsPerPage
              }
            })
          }}
        /> */}
      </React.Fragment>
    );
  }
};

export default WorkshopList;